.docs-sidebar {
  border-right: 1px solid var(--bs-border-color);
  height: 100%;
  padding: 2rem 1rem;
}

.sidebar-heading {
  text-transform: uppercase;
  color: var(--bs-secondary);
  margin-bottom: 1rem;
  font-size: 1rem;
}

.nav-link {
  color: var(--bs-body-color);
  padding: .5rem 0;
}

.nav-link:hover, .nav-link.active {
  color: var(--bs-primary);
}

.docs-content {
  padding: 2rem;
}

.feature-card {
  background: var(--bs-body-bg);
  border: 1px solid var(--bs-border-color);
  border-radius: .5rem;
  padding: 1.5rem;
  transition: transform .2s, box-shadow .2s;
}

.feature-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px #0000001a;
}

.feature-icon {
  color: var(--bs-primary);
  margin-bottom: 1rem;
  font-size: 2rem;
}

.feature-card h3 {
  margin-bottom: .75rem;
  font-size: 1.25rem;
}

.social-links .btn {
  margin-bottom: .5rem;
}

@media (width <= 768px) {
  .docs-sidebar {
    border-right: none;
    border-bottom: 1px solid var(--bs-border-color);
    margin-bottom: 1rem;
  }

  .docs-content {
    padding: 1rem;
  }
}
